.stars {
    display: flex;
  
  
  &__star {
    display: flex;
    height: 16px;
    width: 16px;
    background: url(../images/star.svg) center no-repeat;
    
    &:not(:last-child) {
      margin-right: 4px;
    }
  }

&--1 > :nth-child(-n+1),
&--2 > :nth-child(-n+2),
&--3 > :nth-child(-n+3),
&--4 > :nth-child(-n+4),
&--5 > :nth-child(-n+5) {
  background-image: url(../images/star-active.svg);
  }
}